<template>
  <back-home-btn>
    <template>
      <!-- 天气API 获取天气信息 -->
      <div style="margin-top: 20px">
        <!-- 实时的获取当前时间 -->
        <div>{{ nowTime }}</div>
        <div>{{ goodWhat }}</div>
        <!-- 实时的获取当前时间 -->

        <!-- 获取今日实况天气 -->
        <div class="info-box_unit info-box_unit--user">
          <div class="user-img">
            <img src="@/assets/img/boy.jpg" alt="" />
          </div>
          <div class="user-info">
            <div style="margin: 12px 0">
              <span class="info-dept">{{ name }}</span>
              <span class="info-title" style="margin: 0">
                {{ goodWhat }} !
              </span>
            </div>
            <div class="info-title">{{ dateAndWeek }}</div>
            <div class="info-weather">
              <div style="margin-right: 8px">
                <img style="width: 18px; height: 18px" :src="wea_img" alt="" />
              </div>
              <div style="margin-right: 8px">{{ wea }}</div>
              <div>{{ tem }} ℃</div>
            </div>
          </div>
        </div>
        <!-- 获取今日实况天气 -->
      </div>
      <!-- 天气API 获取天气信息 -->

      <!-- 高德地图 获取天气信息 -->
      <gaoDeWeather ref="gaoDeWeather" />
      <!-- 高德地图 获取天气信息 -->
    </template>
  </back-home-btn>
</template>

<script>
import Dayjs from "dayjs";
import gaoDeWeather from "@/views/weatherApi/gaoDeWeather";

export default {
  name: "group",
  components: { gaoDeWeather },
  props: {},
  computed: {},
  filters: {},
  data() {
    return {
      nowTime: "", // 当前时间
      goodWhat: "", // 招呼语
      name: "userName", // 用户名
      dateAndWeek: "", // 日期
      wea: "", // 天气
      tem: "", // 温度
      wea_img: "", // 天气的图片

      searchForm: {
        timeArr: [],
      },
    };
  },
  methods: {
    // ---------------- ▼ 获取当前时间 ▼ ----------------
    //显示当前时间（年月日时分秒）
    timeFormate(timeStamp) {
      let year = new Date(timeStamp).getFullYear();
      let month =
        new Date(timeStamp).getMonth() + 1 < 10
          ? "0" + (new Date(timeStamp).getMonth() + 1)
          : new Date(timeStamp).getMonth() + 1;
      let date =
        new Date(timeStamp).getDate() < 10
          ? "0" + new Date(timeStamp).getDate()
          : new Date(timeStamp).getDate();
      let hh =
        new Date(timeStamp).getHours() < 10
          ? "0" + new Date(timeStamp).getHours()
          : new Date(timeStamp).getHours();
      let mm =
        new Date(timeStamp).getMinutes() < 10
          ? "0" + new Date(timeStamp).getMinutes()
          : new Date(timeStamp).getMinutes();
      let ss =
        new Date(timeStamp).getSeconds() < 10
          ? "0" + new Date(timeStamp).getSeconds()
          : new Date(timeStamp).getSeconds();
      let week = new Date(timeStamp).getDay();
      let weeks = ["日", "一", "二", "三", "四", "五", "六"];
      let getWeek = "星期" + weeks[week];
      this.nowTime =
        year +
        "年" +
        month +
        "月" +
        date +
        "日" +
        " " +
        hh +
        ":" +
        mm +
        ":" +
        ss +
        getWeek;
    },
    nowTimes() {
      this.timeFormate(new Date());
      setInterval(this.nowTimes, 1000);
      this.clear();
    },
    clear() {
      clearInterval(this.nowTimes);
      this.nowTimes = null;
    },

    /**
     * @Event 方法
     * @description: 判断一个时间是否在指定时间范围内
     * 使用方法： this.time_range('11:32', '11:34'); // 返回true或false;
     * */
    time_range(beginTime, endTime) {
      var strb = beginTime.split(":");
      var stre = endTime.split(":");
      var b = new Date();
      var e = new Date();
      var n = new Date();
      b.setHours(strb[0]);
      b.setMinutes(strb[1]);
      e.setHours(stre[0]);
      e.setMinutes(stre[1]);
      if (n.getTime() - b.getTime() > 0 && n.getTime() - e.getTime() < 0) {
        // console.log("true");
        return true;
      } else {
        // console.log("false");
        return false;
      }
    },
    // ---------------- ▲ 获取当前时间 ▲ ----------------

    // ---------------- ▼ 获取今日实况天气 ▼ ----------------
    // 参考：http://doc.tianqiapi.com/1631612
    getWeaAndTem() {
      this.$axios({
        methods: "get",
        url: "https://tianqiapi.com/free/day",
        headers: {},
        params: {
          appid: "63848323", // 账号ID
          appsecret: "5s7y4KQg", // 账号密钥
          cityid: "101210113", // 城市编号,不要带CN, 以下参数3选1
          city: null, // 城市名称,不要带市和区
          ip: null, // IP地址
          callback: 0, // jsonp方式
        },
      })
        .then((res) => {
          let date = res.data.date;
          date = date.split("-");
          const week = res.data.week;
          this.dateAndWeek =
            date[0] + "年" + date[1] + "月" + date[2] + "日" + " " + week;
          this.wea = res.data.wea;
          this.tem = res.data.tem;
          switch (res.data.wea_img) {
            case "bingbao":
              this.wea_img = require("@/assets/img/ctrip/bingbao.png");
              break;
            case "lei":
              this.wea_img = require("@/assets/img/ctrip/lei.png");
              break;
            case "qing":
              this.wea_img = require("@/assets/img/ctrip/qing.png");
              break;
            case "shachen":
              this.wea_img = require("@/assets/img/ctrip/shachen.png");
              break;
            case "wu":
              this.wea_img = require("@/assets/img/ctrip/wu.png");
              break;
            case "xue":
              this.wea_img = require("@/assets/img/ctrip/xue.png");
              break;
            case "yin":
              this.wea_img = require("@/assets/img/ctrip/yin.png");
              break;
            case "yu":
              this.wea_img = require("@/assets/img/ctrip/yu.png");
              break;
            case "yujiaxue":
              this.wea_img = require("@/assets/img/ctrip/yujiaxue.png");
              break;
            case "yun":
              this.wea_img = require("@/assets/img/ctrip/yun.png");
              break;
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    // ---------------- ▲ 获取今日实况天气 ▲ ----------------

    getNameAndDate() {
      const time_range = function (beginTime, endTime) {
        const strb = beginTime.split(":");
        const stre = endTime.split(":");
        const b = new Date();
        const e = new Date();
        const n = new Date();
        b.setHours(strb[0]);
        b.setMinutes(strb[1]);
        e.setHours(stre[0]);
        e.setMinutes(stre[1]);
        if (n.getTime() - b.getTime() > 0 && n.getTime() - e.getTime() < 0) {
          // console.log("true");
          return true;
        } else {
          // console.log("false");
          return false;
        }
      };
      if (time_range("0:00", "5:01")) this.goodWhat = "凌晨好";
      if (time_range("5:00", "9:01")) this.goodWhat = "早上好";
      if (time_range("9:00", "11:31")) this.goodWhat = "上午好";
      if (time_range("11:30", "14:01")) this.goodWhat = "中午好";
      if (time_range("14:00", "18:31")) this.goodWhat = "下午好";
      if (time_range("18:30", "0:01")) this.goodWhat = "晚上好";
    },

    /**
     * @Event 方法
     * @description: DayJs 使用
     * */
    useDayJs() {
      let time = Dayjs().subtract(1, "month").format("YYYY-MM-DD HH:mm:ss");
      // let time = Dayjs().subtract(1, 'week').format('YYYY-MM-DD HH:mm:ss');
      let time1 = Dayjs().format("YYYY-MM-DD HH:mm:ss");
      this.searchForm.timeArr = [time, time1];
      console.log("timeArr", this.searchForm.timeArr);
    },
  },
  created() {
    this.useDayJs();
  },
  mounted() {
    this.timeFormate();
    this.nowTimes();
    this.getWeaAndTem(); // 实况天气
    this.getNameAndDate(); // 招呼语（上午好，下午好之类的）
  },
  destroyed() {
    this.clear();
  },
};
</script>

